import { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useCart } from "./CartContext";
import axios from "axios";
import moment from "moment";
import { getTodayDate, isFutureOrToday } from "../utils/timeAgo";

const Cart = () => {
  const { cartItems, removeFromCart } = useCart();
  const [appointmentData, setAppointmentData] = useState({});

  const navigate = useNavigate();
  const location = useLocation();
  const previousPage = location.state?.from || "/";

  // ✅ Load appointmentData from localStorage when component mounts
  useEffect(() => {
    const savedData = localStorage.getItem("appointmentDetails");
    if (savedData) {
      setAppointmentData(JSON.parse(savedData));
    }
  }, []);

  // ✅ Update localStorage whenever appointmentData changes
  useEffect(() => {
    localStorage.setItem("appointmentDetails", JSON.stringify(appointmentData));
  }, [appointmentData]);

  const handleRemove = (item) => {
    const id =
      item?.type === "service" ? item?.service?._id : item?.package?._id;

    // Remove from cart
    removeFromCart(id, item?.type);

    // Also remove from appointmentData
    const updated = { ...appointmentData };
    delete updated[id];
    setAppointmentData(updated);
  };

  const handleDateChange = (id, date) => {
    setAppointmentData((prev) => ({
      ...prev,
      [id]: {
        ...prev[id],
        date,
        time: "",
      },
    }));
  };

  useEffect(() => {
    const clearOnRefresh = () => {
      localStorage.removeItem("appointmentDetails");
    };

    window.addEventListener("beforeunload", clearOnRefresh);
    return () => {
      window.removeEventListener("beforeunload", clearOnRefresh);
    };
  }, []);

  const handleTimeChange = (id, time) => {
    const selectedDate = appointmentData[id]?.date || null;

    setAppointmentData((prev) => ({
      ...prev,
      [id]: {
        ...(prev[id] || {}),
        time,
      },
    }));
  };

  const handleNextClick = () => {
    if (cartItems.length === 0) {
      alert("Please select at least one booking to proceed forward");
      return;
    }
    let isValid = true;

    cartItems.forEach((item) => {
      const id =
        item.type === "service" ? item.service?._id : item.package?._id;
      const appt = appointmentData[id] || {};
      if (!appt.date || !appt.time) {
        isValid = false;
      }
    });

    if (!isValid) {
      alert(
        "Please select both date and time for all items before proceeding."
      );
      return;
    }

    const isLoggedIn = localStorage.getItem("token");
    if (isLoggedIn) {
      navigate("/cart-personal");
    } else {
      navigate("/user-login");
    }
  };

  // Fetching data & time from backend for service & package
  const [serviceSlots, setServiceSlots] = useState({});

  const fetchItemsofCart = async (id, type) => {
    if (!id || !type) {
      console.error("Missing id or type");
      return;
    }

    try {
      let url = "";
      if (type === "service") {
        url = `${process.env.REACT_APP_BACKEND_URL}/api/centreService/single/${id}`;
      } else if (type === "package") {
        url = `${process.env.REACT_APP_BACKEND_URL}/api/packages/single/${id}`;
      } else {
        console.error("Invalid type:", type);
        return;
      }

      const res = await axios.get(url);

      const slotsData =
        type === "service"
          ? res.data?.service?.slots
          : res.data?.package?.slots;

      setServiceSlots((prev) => ({ ...prev, [id]: slotsData || [] }));
      return res.data;
    } catch (err) {
      console.error("Error fetching item from cart:", err);
    }
  };

  function formatTime24To12(time24) {
    if (
      time24.toUpperCase().includes("AM") ||
      time24.toUpperCase().includes("PM")
    ) {
      return time24.replace(/\s+/g, "");
    }
    const [hourStr, minute] = time24.split(":");
    let hour = parseInt(hourStr, 10);
    const ampm = hour >= 12 ? "PM" : "AM";
    hour = hour % 12 || 12;
    return `${hour}:${minute}${ampm}`;
  }

  function formatSlot(slot) {
    if (typeof slot === "string") {
      const [from, to] = slot.split("-");
      return `${formatTime24To12(from)}-${formatTime24To12(to)}`;
    } else if (slot?.from && slot?.to) {
      return `${formatTime24To12(slot.from)}-${formatTime24To12(slot.to)}`;
    } else {
      return String(slot);
    }
  }

  function getDayName(dateString) {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", { weekday: "long" });
  }

  useEffect(() => {
    cartItems.forEach((item) => {
      const isService = item.type === "service";
      const id = isService ? item.service?._id : item.package?._id;
      if (id) {
        fetchItemsofCart(id, item.type);
      }
    });
  }, [cartItems]);

  function isSlotAvailable(slot, selectedDate) {
    if (!selectedDate) return true;

    let fromTimeStr = "";
    if (typeof slot === "string") {
      fromTimeStr = slot.split("-")[0];
    } else if (slot.from) {
      fromTimeStr = slot.from;
    } else {
      return true;
    }

    const slotTime = moment(
      `${selectedDate} ${fromTimeStr}`,
      "YYYY-MM-DD hh:mm A"
    );

    if (moment(selectedDate, "YYYY-MM-DD").isSame(moment(), "day")) {
      return slotTime.isAfter(moment().add(2, "hours"));
    }

    return true;
  }

  const toTitleCase = (str) => {
    return str
      .toLowerCase()
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  return (
    <div className="cart padding-y-120">
      <div className="container">
        <div className="cart-content" style={{ marginTop: "-80px" }}>
          <div className="table-responsive">
            <p style={{ textAlign: "center", paddingBottom: "40px" }}>
              Please choose a time slot at least 2 hours from now.
            </p>
            <table className="table style-two">
              <thead>
                <tr>
                  <th>Service Details</th>
                  <th>Appointment Date</th>
                  <th>Appointment Time</th>
                  <th>Service Cost(₹)</th>
                </tr>
              </thead>

              <tbody>
                {cartItems.length === 0 ? (
                  <tr>
                    <td colSpan="4" className="text-center text-muted py-4">
                      No booking has been done.
                    </td>
                  </tr>
                ) : (
                  cartItems.map((item, index) => {
                    const isService = item.type === "service";
                    const isPackage = item.type === "package";
                    const data = isService ? item.service : item.package;
                    const itemId = data?._id;

                    const selectedDate = appointmentData[itemId]?.date;
                    const selectedDay = selectedDate
                      ? getDayName(selectedDate)
                      : null;

                    const allSlots = serviceSlots[itemId] || [];

                    let filteredSlots = selectedDay
                      ? allSlots.filter(
                          (slot) =>
                            slot.day.trim().toLowerCase() ===
                            selectedDay.toLowerCase()
                        )
                      : [];

                    filteredSlots = filteredSlots.filter((slotObj) =>
                      isSlotAvailable(slotObj, selectedDate)
                    );

                    filteredSlots.sort((a, b) => {
                      const timeA = moment(a.from, "hh:mm A");
                      const timeB = moment(b.from, "hh:mm A");
                      return timeA - timeB;
                    });

                    const noSlotsForDay =
                      selectedDate && filteredSlots.length === 0;

                    return (
                      <tr key={item._id || index}>
                        <td>
                          <div className="cart-item">
                            <div className="d-flex align-items-center gap-3">
                              <div className="cart-item__content">
                                <h6 className="cart-item__title font-heading fw-700 text-capitalize font-18 mb-4">
                                  {isService ? (
                                    <Link
                                      to={`/service-details/${data?._id}`}
                                      className="link"
                                    >
                                      {toTitleCase(data?.serviceName)}
                                    </Link>
                                  ) : (
                                    <Link
                                      to={`/package-details/${data?._id}`}
                                      className="link"
                                    >
                                      {toTitleCase(data?.packageName)}
                                    </Link>
                                  )}
                                </h6>

                                {isService && (
                                  <p className="text-muted font-14 mb-2">
                                    {toTitleCase(data?.serviceCategory)}
                                  </p>
                                )}

                                <span
                                  className="cart-item__price font-18 text-heading fw-500"
                                  style={{ marginTop: "-5px" }}
                                >
                                  <span className="text-body font-14">
                                    {toTitleCase(data?.centre?.centreName)}
                                  </span>
                                  <br />
                                  <span className="text-body font-14">
                                    {data?.doctors?.[0]?.name}
                                  </span>
                                </span>
                              </div>
                            </div>

                            <div className="flx-align gap-4 mt-3 mt-lg-4">
                              <button
                                type="button"
                                className="btn btn-sm btn-danger"
                                onClick={() => handleRemove(item)}
                              >
                                Remove
                              </button>
                            </div>
                          </div>
                        </td>

                        {isService ? (
                          <>
                            <td>
                              <input
                                type="date"
                                className="common-input radius-8 common-input--md"
                                // min={new Date().toISOString().split("T")[0]}
                                min={getTodayDate()} // ✅ Works properly across devices
                                value={selectedDate || ""}
                                onChange={(e) => {
                                  const val = e.target.value;

                                  if (!isFutureOrToday(val)) {
                                    e.target.value = getTodayDate();
                                    alert(
                                      "Please select a valid date (today or future)."
                                    );
                                    return;
                                  }

                                  handleDateChange(itemId, val);
                                }}
                                onBlur={(e) => {
                                  // Fallback for Safari that ignores min on picker open
                                  if (!isFutureOrToday(e.target.value)) {
                                    e.target.value = getTodayDate();
                                  }
                                }}
                                required
                              />
                            </td>

                            <td>
                              <div className="select-has-icon">
                                <select
                                  className="common-input border"
                                  value={appointmentData[itemId]?.time || ""}
                                  onChange={(e) =>
                                    handleTimeChange(itemId, e.target.value)
                                  }
                                  onClick={() =>
                                    fetchItemsofCart(itemId, item.type)
                                  }
                                  disabled={noSlotsForDay}
                                  // style={{height:"15px"}}
                                >
                                  <option value="">Select Slot</option>
                                  {filteredSlots.map((slotObj, idx) => {
                                    const formattedSlot = `${slotObj.from} - ${slotObj.to}`;
                                    return (
                                      <option key={idx} value={formattedSlot}>
                                        {formattedSlot}
                                      </option>
                                    );
                                  })}
                                </select>
                              </div>
                              {noSlotsForDay && (
                                <p
                                  className="text-danger"
                                  style={{ fontSize: "0.875rem" }}
                                >
                                  No appointment slots available on this day.
                                </p>
                              )}
                        
                            </td>
                          </>
                        ) : (
                          <>
                            <td>
                              <input
                                type="date"
                                className="common-input radius-8 common-input--md"
                                value={appointmentData[itemId]?.date || ""}
                                // min={new Date().toISOString().split("T")[0]}
                                min={getTodayDate()}
                                onChange={(e) => {
                                  const val = e.target.value;

                                  if (!isFutureOrToday(val)) {
                                    e.target.value = getTodayDate();
                                    alert(
                                      "Please select a valid date (today or future)."
                                    );
                                    return;
                                  }

                                  handleDateChange(itemId, val);
                                }}
                                onBlur={(e) => {
                                  // Fallback for Safari that ignores min on picker open
                                  if (!isFutureOrToday(e.target.value)) {
                                    e.target.value = getTodayDate();
                                  }
                                }}
                                required
                              />
                            </td>

                            <td>
                              <div className="select-has-icon">
                                <select
                                  className="common-input border"
                                  value={appointmentData[itemId]?.time || ""}
                                  onChange={(e) =>
                                    handleTimeChange(itemId, e.target.value)
                                  }
                                  onClick={() =>
                                    fetchItemsofCart(item._id, item.type)
                                  }
                                  disabled={noSlotsForDay}
                                  required
                                >
                                  <option value="">Select Slot</option>
                                  {filteredSlots.map((slotObj, idx) => {
                                    const formattedSlot = formatSlot(slotObj);
                                    return (
                                      <option key={idx} value={formattedSlot}>
                                        {formattedSlot}
                                      </option>
                                    );
                                  })}
                                </select>
                              </div>
                              {noSlotsForDay && (
                                <p
                                  className="text-danger"
                                  style={{ fontSize: "0.875rem" }}
                                >
                                  No appointment slots available on this day.
                                </p>
                              )}
                            </td>
                          </>
                        )}

                        <td style={{ textAlign: "center" }}>
                          <span className="cart-item__totalPrice text-body font-18 fw-400 mb-0">
                            {" "}
                            ₹{data?.totalPrice}
                          </span>
                        </td>
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>

          <div className="cart-content__bottom flx-between gap-2">
            <Link
              to={previousPage}
              className="btn btn-outline-light flx-align gap-2 pill btn-lg"
            >
              <span className="icon line-height-1 font-20">
                <i className="fas fa-arrow-left" />
              </span>
              Book Other Service
            </Link>

            <button
              className="btn btn-main flx-align gap-2 pill btn-lg"
              onClick={handleNextClick}
            >
              Next
              <span className="icon line-height-1 font-20">
                <i className="fas fa-arrow-right" />
              </span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart;
